
body {
  margin: 0;
}
footer {
  background-color: #333333;
  color: white;
  font-size: large;
  height: 50px;
}
footer > div {
    padding: 14px 16px;
}
.content {
  min-height: calc(100vh - 70px);
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
@media screen and (min-width: 860px) {
.content > div, footer, .content > section {
    padding: 0 5%;
}
}
#nav {
  background-color: #333333;
}
#nav img {
    width: 100px;
    display: inline-block;
}
#nav ul {
    margin: 0;
    list-style-type: none;
    overflow: hidden;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding: 0;
}
#nav li {
    float: left;
}
#nav li a {
      display: block;
      color: white;
      text-align: center;
      padding: 14px 16px;
      text-decoration: none;
}
#nav li a:hover {
        color: gray;
}

